@import url("https://fonts.googleapis.com/css2?family=Roboto:wght@300&display=swap");
body {
  font-family: "Roboto", "Arial", sans-serif;
}
.site {
  min-height: 100vh;
  margin-bottom: -40px;
}

footer,
.divSite {
  height: 40px;
}

.bgLightBrown {
  background-color: #f6f6f6;
}

.textColorBrown {
  color: #c9c2b8;
}

.bgBeige {
  background-color: #e0dbd1;
}

.textColorBeige {
  color: #fafaf9;
}

.titleBio {
  color: #90837a;
}

.paragraphBio {
  color: #978d83;
}

.myMail {
  text-decoration: none;
  color: #f6f6f6;
}
.myMail:hover {
  color: #978d83;
}

.contactComponent {
  text-align: center;
  margin-top: 30vh;
}

/* CSS Projet */

#projet {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
}

.projets {
  width: 400px;
  margin: 30px;
  display: inline-block;
  background: #ffffff;
  padding-bottom: 15px;
  border: 0px #472b2b solid;
  border-radius: 10px;
  overflow: hidden;
  box-shadow: 0 3px 6px rgba(88, 62, 62, 0.16), 0 3px 6px rgba(81, 51, 51, 0.23);
}

.projets:hover {
  box-shadow: 0 14px 28px rgba(90, 67, 67, 0.25),
    0 10px 10px rgba(90, 60, 60, 0.22);
  transition: 0.5s;
}

.picture {
  width: 400px;
  height: 266px;
  overflow: hidden;
  margin-bottom: 15px;
}

.projets h3 {
  text-transform: uppercase;
  display: block;
}

.description-project {
  text-align: center;
  color: #90837a;
}
